@font-face {
  font-family: "GmarketSansLight";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/GmarketSansLight.woff")
    format("woff");
  font-weight: normal;
  font-style: normal;
}

.container {
  margin: auto;
  padding: 5vh 10vh;
  width: 100%;
  height: fit-content;
}

.title {
  margin: 100px 0;
  font-size: 70px;
  font-weight: 600;
  font-family: "GmarketSansLight";
  text-align: left;
}

.skill__block {
  text-align: left;
}

.description p {
  line-height: 30px;
  text-indent: 5px;
  font-family: "GmarketSansLight";
}

.img__box {
  margin-top: 3vh;
  text-align: right;
}

.skill__block .image {
  margin: 15px 7px 5px 7px;
  padding-top: 5px;
  width: 13vh;
  height: 13vh;
}

.skill__block .image2 {
  margin: 15px 7px 5px 7px;
  padding-top: 5px;
  width: 15vh;
  height: 13vh;
}

.skill__block .image:hover {
  transform: scale(1.3);
}

.skill__block .image2:hover {
  transform: scale(1.3);
}
